import React,{Component} from 'react';
import CSSModules from 'react-css-modules';
import styles from './styles/banner.css';
import banner from './img/banner.png';
import serviceIcon from './img/service-icon.png';

let Item=function (props) {
    return (
        <div className="lf" styleName="icon-block">
            <img src={props.icon} alt="" styleName="icon"/>
            <span styleName="icon-tips">{props.name}</span>
        </div>
    )
};
Item=CSSModules(Item,styles);
let Divider=function (props) {
    return (
        <div className="lf" style={{display:"inline-block",background:"#4188a8",width:"1px",height:"1.2rem"}}/>
    )
};
class Banner extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div styleName="root">
                <div className="clearfix" styleName="top-area">
                    <div className="lf">
                        <p styleName="title"> </p>
                        <p styleName="tips"></p>
                        <p styleName="tips"></p>
                    </div>
                    <div className="rt">
                        <img src={banner} alt="rocket" styleName="banner"/>
                    </div>
                </div>
                <div className="clearfix" styleName="bottom-area">
                    <div className="clearfix" styleName="content">
                        <Divider/>
                        <Item icon={serviceIcon} name=""/>
                        <Divider/>
                        <Item icon={serviceIcon} name=""/>
                        <Divider/>
                        <Item icon={serviceIcon} name=""/>
                        <Divider/>
                        <Item icon={serviceIcon} name=""/>
                        <Divider/>
                    </div>
                </div>
            </div>
        )
    }
}
export default CSSModules(Banner,styles);


