import React,{Component} from 'react';

const simulationData=[
    {category: "Sporting Goods", price: "$49.99", stocked: true, name: "Football"},
    {category: "Sporting Goods", price: "$9.99", stocked: true, name: "Baseball"},
    {category: "Sporting Goods", price: "$29.99", stocked: false, name: "Basketball"},
    {category: "Electronics", price: "$99.99", stocked: true, name: "iPod Touch"},
    {category: "Electronics", price: "$399.99", stocked: false, name: "iPhone 5"},
    {category: "Electronics", price: "$199.99", stocked: true, name: "Nexus 7"}
];
function dataClassify(data) {
    const result={};
    data.map(item=>{
        const cate=item.category;
        if(!result[cate]){
            result[cate]=[];
        }
        return result[cate].push(item);
    });
    return result;
}
function ProductRow(props) {
    const {name,price}=props.info;
    return (
        <div>
            <span>{name}</span>
            <span>{price}</span>
        </div>
    )
}
function ProductCategoryRow(props) {
    return (
        <h3>{1 && props.category} {String(true)}</h3>
    )
}
function ProductTable(props) {
    const {filterText,isStockOnly,classificationData}=props;
    let productData=[];
    classificationData.forEach((item,index)=>{
        let flag=true;
        if(isStockOnly && !item.stocked){
            flag=false;
        }
        if(!!filterText && item.name.indexOf(filterText) === -1 ){
            flag=false;
        }
        flag && productData.push(item);
    });
    productData=dataClassify(productData);
    return (
        <div>
            <span>Name</span>
            <span>Price</span>
            {
                Object.entries(productData).map(([key,value])=>{
                    return <div key={key}>
                        <ProductCategoryRow category={key}/>
                        {
                            value.map((item, index) => {
                                return <ProductRow key={index} info={item}/>
                            })
                        }
                    </div>
                })
            }
        </div>
    )
}
function SearchBar(props) {
    function handleFilterText(e){
        props.handleStateChange({filterText:e.target.value});
    }
    function handleIsStockOnly(e) {
        props.handleStateChange({isStockOnly:e.target.checked});
    }
    return(
        <div>
            <input type="text" onChange={handleFilterText} value={props.filterText} placeholder="Search..."/>
            <div>
                <input type="checkbox" onChange={handleIsStockOnly} id="filterStock" checked={props.isStockOnly}/>
                <label htmlFor="filterStock">Only show products in stock</label>
            </div>
        </div>
    )
}
class FilterableProductTable extends Component{
    constructor(props){
        super(props);
        this.state={
            filterText:'',
            isStockOnly:false
        };
        this.handleStateChange=this.handleStateChange.bind(this);
        this.result=simulationData;
    }
    handleStateChange(obj){
        this.setState(obj);
    }
    render(){
        const {filterText,isStockOnly}=this.state;
        return(
            <div>
                <SearchBar filterText={filterText} isStockOnly={isStockOnly} handleStateChange={this.handleStateChange}/>
                <ProductTable filterText={filterText} isStockOnly={isStockOnly} classificationData={this.result}/>
            </div>
        )
    }
}
export default FilterableProductTable;


